
.heading {
  line-height: 1.3;
}
.heading--xx-large {
  font-size: 32px;
}
.heading--x-large {
  font-size: 24px;
}
.heading--large {
  font-size: 22px;
  color: #5A5D5E;
  font-weight: 300;
  padding: 0 20% 0 0;
}

h4, h5, h6 {
  margin: 0;
  padding: 10px 0;
}
.heading--small {
  font-size: 18px;
}
.heading--x-small {
  font-size: 16px;
}
.heading--xx-small {
  font-size: 13px;
  font-weight: 300;
}

p {
  margin: 0;
}
